#Home {
    max-width: 1000px;
    margin: 0 auto;
    padding: 0 16px;

    >header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-bottom: 1px solid #ddd;
        padding: 16px 0;
    }

    >main {
        padding: 16px 0;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
    }
}